.customer {
    margin-top: 3rem;
    margin-bottom: 3rem;
}

.customer-grid {
    display: grid;
    grid-template-columns: 25% 75%;
    grid-column-gap: 1.5rem;
}

.customer-card,
.moments-card {
    background: none;
    border-radius: 7px;
    background-color: $white-color;
}

.customer-card {
    height: 240px;
    font-size: 14px;
    text-align: center;
    border: 1px solid rgba(92, 92, 92, 0.2);
}

.customer-card img{
    border-radius: 50%;
}

.moments-card {
    border: 1px solid rgba(92, 92, 92, 0.2);
}

.moments-card h3 {
    color: $text-color;
    margin: 1rem;
    font-size: 1.2rem;
    font-weight: normal;
}